import React, {useEffect, useState} from 'react';
import Friends from "./Friends";
import {connect} from "react-redux";
import {requestFriends} from "../../../../redux/sideBarReducer";
import {compose} from "redux";


const FriendsContainer = (props) => {
   // let [localFriends, setLocalFriends] = useState(props.friends);

   useEffect(() => {
      props.requestFriends()
   }, [])

   const isOwner = props.authorizedUserId;
   return isOwner && <Friends {...props}/>
}

const mapStateToProps = (state) => {
   return {
      friends: state.sideBarReducer.friends,
      authorizedUserId: state.authReducer.id
   }
}

export default compose(
   connect(
      mapStateToProps,
      {requestFriends}
   )
)(FriendsContainer)