import {usersAPI} from "../api/api";
import {setCurrentPage, setTotalUserCount, setUsers, toggleIsFetching} from "./usersReducer";

const SET_FRIENDS = 'my-react/sideBar/SET_FRIENDS';


let initialState = {
   friends: []
   // friends: [
   //    {id: 1, name: 'Ivan', ava: null},
   //    {id: 2, name: 'Victoria', ava: 'https://i.redd.it/ahg5rdrp9vxz.jpg'},
   //    {
   //       id: 3,
   //       name: 'Valeria',
   //       ava: null
   //    }
   // ]
};

const sideBarReducer = (state = initialState, action) => {
   switch (action.type) {
      case SET_FRIENDS:
         return {
            ...state, friends: action.friends
         }
      default:
         return state;
   }
}

export const setFriends = (friends) => ({type: SET_FRIENDS, friends})

export const requestFriends = (friend) => async (dispatch) => {
   // dispatch(toggleIsFetching(true));
   const data = await usersAPI.requestFriends(friend);
   // dispatch(toggleIsFetching(false));
   dispatch(setFriends(data.items));
};

export default sideBarReducer;